import React, { useEffect, useState } from 'react';
import axios from 'axios';

const ProfilePage = ({ userId }) => {
  const [profile, setProfile] = useState({
    lastName: '',
    firstName: '',
    phone1: '',
    phone2: '',
    phone3: '',
    address: '',
  });

  const [media, setMedia] = useState([]);
  const [avatarFile, setAvatarFile] = useState(null);
  const [photoFiles, setPhotoFiles] = useState({ 1: null, 2: null, 3: null });

  useEffect(() => {
    axios.get(`/api/profile/${userId}`).then(res => setProfile(res.data));
    axios.get(`/api/media-profile/${userId}`).then(res => setMedia(res.data));
  }, [userId]);

  const handleChange = (e) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    axios.put(`/api/profile/${userId}`, profile).then(() => {
      alert('Profil mis  jour');
    });
  };

  const handleUpload = (slot, file) => {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('profileId', userId);
    formData.append('slot', slot);

    axios.post('/api/media-profile/upload', formData).then(() => {
      alert(`Mdia slot ${slot} ajout`);
    });
  };

  const handleDelete = (mediaId) => {
    axios.delete(`/api/media-profile/${mediaId}`).then(() => {
      alert('Mdia supprim');
    });
  };

  const avatar = media.find(m => m.slot === 0);
  const photos = media.filter(m => [1, 2, 3].includes(m.slot));

  return (
    <div>
      <h2>Profil utilisateur</h2>
      <form onSubmit={handleSubmit}>
        <input type="text" name="lastName" value={profile.lastName} onChange={handleChange} placeholder="Nom" />
        <input type="text" name="firstName" value={profile.firstName} onChange={handleChange} placeholder="Prnom" />
        <input type="text" name="phone1" value={profile.phone1} onChange={handleChange} placeholder="Tlphone 1" />
        <input type="text" name="phone2" value={profile.phone2} onChange={handleChange} placeholder="Tlphone 2" />
        <input type="text" name="phone3" value={profile.phone3} onChange={handleChange} placeholder="Tlphone 3" />
        <textarea name="address" value={profile.address} onChange={handleChange} placeholder="Adresse" />
        <button type="submit">Enregistrer</button>
      </form>

      <h3>Avatar</h3>
      {avatar && (
        <div>
          <img src={avatar.path} alt="Avatar" />
          <button onClick={() => handleDelete(avatar.id)}>Supprimer</button>
        </div>
      )}
      <input type="file" onChange={(e) => handleUpload(0, e.target.files[0])} />

      <h3>Galerie photos</h3>
      {photos.map(p => (
        <div key={p.id}>
          <img src={p.path} alt={`Photo ${p.slot}`} />
          <button onClick={() => handleDelete(p.id)}>Supprimer</button>
        </div>
      ))}
      {[1, 2, 3].map(slot => (
        <div key={slot}>
          <label>Ajouter photo {slot}</label>
          <input type="file" onChange={(e) => handleUpload(slot, e.target.files[0])} />
        </div>
      ))}
    </div>
  );
};

export default ProfilePage;
